.StartRendering_Container {
    display: flex;

    width: 100%;
    height: calc(var(--vh, 1vh) * 100);

    align-items: center;
    margin : 0 auto;
}

.StartRendering_Wrapper {
    display: flex;
    flex-direction: column;

    width: 100%;
    height : 661px;

    background: #1E1F23;
}

.StartRendering_Back {
    width : 14px;
    height : 24px;

    margin-top : 20px;
    margin-left : 20px;
    margin-right : auto;
}

.StartRendering_TextBox_LayOut {
    display: flex;
    flex-direction: column;

    margin-left : 20px;
    margin-right : auto;

    color: #FFF;
    font-family: Pretendard;

    .StartRendering_TextBox1 {
        display: flex;
        width: 300px;
        font-size: 28px;
        font-weight: 700;
        margin-bottom: 20px;
    }
    .StartRendering_TextBox2 {
        font-size: 16px;
        font-weight: 400;
    }
}

.StartRendering_Btn {
    display: flex;
    width: 350px;
    height : 50px;

    justify-content: center;
    align-items: center;

    border-radius: 16px;
    background: #DDFD5C;
    border : none;

    color: #000;
    font-family: Inter;
    font-size: 18px;
    font-weight: 600;

    margin : 0 auto;
}

.StartRendering_Img {
    display: flex;
    width: 178px;
    height: 178px;

    margin : 126px auto;
}

.StartRendering_Img_Undefined {
    display: flex;
    width: 178px;
    height: 178px;

    margin : 105px auto;
}